import { Component, OnInit, ElementRef, OnDestroy } from "@angular/core";
import { ActivatedRoute } from '@angular/router';
import { NormalService } from '../normal.service';
import { Observable } from 'rxjs';
import { BreakpointObserver, Breakpoints } from '@angular/cdk/layout';
import { map } from 'rxjs/operators';

declare const Swiper;

@Component({
    selector: 'front',
    styleUrls: ['./front.style.scss'],
    templateUrl: './front.template.html',
})
export class FrontComponent implements OnInit, OnDestroy {
    handSet: boolean;
    public swipers: any;
    public notices: any;
    public listLotteries: any[]=[];
    public listLottorNames: any[];
    public winnerInfos: any = [];

    displayedColumns = ['MemberName', 'Amount'];

    isHandset$: Observable<boolean> = this.breakpointObserver.observe(Breakpoints.Handset)
    .pipe(
      map(result => result.matches)
    );

    fastNavIconHover: boolean[] = [false, false, false, false];
    mySwiper: any;
    day: Date = new Date();
    constructor (
        router: ActivatedRoute,
        private el: ElementRef,
        private breakpointObserver: BreakpointObserver,
        public normalService: NormalService
    ) {
        router.params.subscribe(params=>{
            // console.log("params:", params)
        })
        this.getSwiper();
        this.getNotices();
        this.getLotteries();
        this.getWinners();
    }
    ngOnInit() {
        this.isHandset$.subscribe(handSet=>{
            this.handSet = handSet;
        });
        this.normalService.winners$.subscribe(winner=> {
            this.winnerInfos.push(winner);
        })
    }
    ngOnDestroy() {
        
    }

    getSwiper(): void {
        this.normalService.getSwiper()
            .subscribe(swipers => {
                this.swipers = swipers.data.Records;
                setTimeout(()=>{
                    this.mySwiper = new Swiper('.swiper-container1', {
                        autoplay: true,//可选选项，自动滑动
                    })
                }, 1000)
            });
    }

    getNotices(): void {
        if (this.normalService.notices.length) {
            return;
        } else {
            this.normalService.getNotices()
                .subscribe(notices => {
                    this.notices = notices.data.Records
                })
        }
    }

    getLotteries(): void {
        if (this.normalService.listLotteries.length>0) {
            this.listLotteries = this.normalService.listLotteries.slice(0,5);
            this.listLottorNames = this.normalService.listLotteries.slice(5);
            this.normalService.listLotteries.forEach(element => {
                this.normalService.getOpenData(String(element.Id))
            });
        } else {
            this.normalService.getLotteries()
                .subscribe(lotteries => {
                    this.listLotteries = lotteries.data.Records.slice(0,5);
                    this.listLottorNames = lotteries.data.Records.slice(5);
                    this.normalService.listLotteries = lotteries.data.Records;
                    this.normalService.listLotteries.forEach(element => {
                        this.normalService.getOpenData(String(element.Id))
                    });
                })
        }
    }

    getWinners(): void {
        this.normalService.getWinners()
            .subscribe(winners => {
                this.winnerInfos = winners.data.Records;
                this.winnerInfos.forEach(element => {
                    this.winnerInfos.push(element);
                });
                this.winnerStartMotion();
            })
    }

    getTimeToOpen(id) {
        let obj = this.normalService.openDatas[id]
        if (obj) {
            let second = obj.timeleft+obj.delaySecond;
            second<=0?second=0:second;
            let hour = Math.floor(second/3600);
            let minute = Math.floor( (second%3600)/60 );
            return `${hour>10?'':'0'}${hour}:${minute>10?'':'0'}${minute}:${(second%60)<10?"0":""}${(second%60)}`
        } else {
            return `00:00:00`;
        }
    }

    async winnerStartMotion() {
        await new Promise((resolve, reject)=>{
            setTimeout(()=>{
                resolve()
            },200)
        })
        let tBody = this.el.nativeElement.querySelector('tbody');
        let difference = tBody.clientHeight - 344;
        let trans = 0;
        setInterval(()=>{
            trans += 1;
            tBody.style.transform = `translate(0, -${trans}px)`;
            if (trans >= difference) trans = 0;
        },50)
    }

    toPlay(lottery) {
        // console.log(lottery);
    }

    fastNavIconIn(id) {
        this.fastNavIconHover[id] = true;
    }

    fastNavIconBlur(id) {
        this.fastNavIconHover[id] = false;
    }

}