import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';

import {RouterModule} from '@angular/router';
import {FormsModule} from "@angular/forms";
import { FrontComponent } from './front.component';
import { LotteryListsComponent } from './lotteryLists/lotterLists.component';
import { HistorysComponent } from './historys/historys.component';
import { FlexLayoutModule } from '@angular/flex-layout';
import { MatToolbarModule, MatListModule, MatIconModule, MatTableModule, MatTabsModule } from '@angular/material';
import { OpenInfosComponent } from './openInfos/openInfos.component';


export const routes = [
    {path: '', component: FrontComponent, pathMatch: 'full'},
    {path: 'list', component: LotteryListsComponent},
    {path: 'list/:type', component: LotteryListsComponent},
    {path: 'historys', component: HistorysComponent},
    {path: 'historys/:name', component: HistorysComponent},
    {path: 'historys/:name/:type', component: HistorysComponent},
    {path: 'openinfo', component: OpenInfosComponent}
];

@NgModule({
    imports: [
        CommonModule,
        RouterModule.forChild(routes),
        FormsModule,
        FlexLayoutModule,
        MatToolbarModule,
        MatListModule,
        MatIconModule,
        MatTabsModule,
        MatTableModule,
    ],
    declarations: [
        FrontComponent,
        LotteryListsComponent,
        HistorysComponent,
        OpenInfosComponent
    ],
})
export class CaipiaoHandyFrontModule {
    static routes = routes;
}
